/* Tooltip.css */
.tooltip-container {
    position: relative;
    display: inline-block;
}

.tooltip {
    position: absolute;
    padding: 5px;
    background-color: #333;
    color: white;
    border-radius: 4px;
    z-index: 1;
    visibility: visible;
}

.tooltip.top {
    bottom: 100%;
    left: 50%;
    transform: translateX(-50%);
}

.tooltip.bottom {
    top: 100%;
    left: 50%;
    transform: translateX(-50%);
}

.tooltip.left {
    top: 50%;
    right: 100%;
    transform: translateY(-50%);
}

.tooltip.right {
    top: 50%;
    left: 100%;
    transform: translateY(-50%);
}

.tooltip span {
    white-space: nowrap;
}

/* Tooltip.css */
.tooltip-container {
    position: relative;
    display: inline-block;
    margin: 100px;
}

.tooltip {
    position: absolute;
    padding: 5px;
    background-color: #333;
    color: white;
    border-radius: 4px;
    z-index: 1;
    visibility: visible;
    margin: 0px 15px;
}

.tooltip.top {
    bottom: 100%;
    left: 50%;
    transform: translateX(-50%);
}

.tooltip.bottom {
    top: 100%;
    left: 50%;
    transform: translateX(-50%);
}

.tooltip.left {
    top: 50%;
    right: 100%;
    transform: translateY(-50%);
}

.tooltip.right {
    top: 50%;
    left: 100%;
    transform: translateY(-50%);
    padding: 15px;
}

.tooltip span {
    white-space: nowrap;
}